"use client";
import { useState } from "react";
import { useRouter } from "next/navigation";
import { BiUpload } from "react-icons/bi";
import { campaignService } from "@/services/campaignService";
import { uploadService } from "@/services/uploadService";
import useAuthStore from "@/store/authStore";
import toast from "react-hot-toast";

export default function UploadFundraiserForm() {
  const router = useRouter();
  const { user } = useAuthStore();
  
  const [formData, setFormData] = useState({
    mainPicture: null,
    summary: "",
    heading: "",
    contact: "",
    startDate: "",
    targetAmount: "",
    description: "",
    video1: "",
    video2: "",
    video3: "",
    image1: null,
    image2: null,
    image3: null
  });

  const [isSubmitting, setIsSubmitting] = useState(false);
  const [uploadProgress, setUploadProgress] = useState(0);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  const handleFileChange = (e, fieldName) => {
    const file = e.target.files[0];
    if (file) {
      // Validate file size (max 5MB)
      if (file.size > 5 * 1024 * 1024) {
        toast.error('File size should be less than 5MB');
        return;
      }
      setFormData(prev => ({ ...prev, [fieldName]: file }));
    }
  };

  const uploadImage = async (file) => {
    if (!file) return null;
    
    try {
      const formData = new FormData();
      formData.append('image', file);
      
      const response = await uploadService.uploadSingle(formData);
      return response.data.url;
    } catch (error) {
      console.error('Image upload failed:', error);
      throw error;
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    // Validation - Check all required fields
    if (!formData.heading || !formData.targetAmount || !formData.summary || !formData.description) {
      toast.error('Please fill all required fields');
      return;
    }

    // Title validation (10-100 characters)
    if (formData.heading.length < 10) {
      toast.error('Fundraiser heading must be at least 10 characters');
      return;
    }
    if (formData.heading.length > 100) {
      toast.error('Fundraiser heading cannot exceed 100 characters');
      return;
    }

    // Short description validation (20-200 characters)
    if (formData.summary.length < 20) {
      toast.error('Fundraiser summary must be at least 20 characters');
      return;
    }
    if (formData.summary.length > 200) {
      toast.error('Fundraiser summary cannot exceed 200 characters');
      return;
    }

    // Description validation (50-5000 characters)
    if (formData.description.length < 50) {
      toast.error('Fundraiser description must be at least 50 characters');
      return;
    }
    if (formData.description.length > 5000) {
      toast.error('Fundraiser description cannot exceed 5000 characters');
      return;
    }

    // Goal amount validation
    if (formData.targetAmount < 100) {
      toast.error('Target amount must be at least â‚¹100');
      return;
    }

    if (!formData.mainPicture) {
      toast.error('Please upload main picture');
      return;
    }

    if (!formData.contact) {
      toast.error('Please provide contact number');
      return;
    }

    // Validate contact number (10 digits)
    const phoneRegex = /^[6-9]\d{9}$/;
    if (!phoneRegex.test(formData.contact.replace(/\D/g, ''))) {
      toast.error('Please provide a valid 10-digit contact number starting with 6-9');
      return;
    }

    if (!formData.startDate) {
      toast.error('Please select campaign start date');
      return;
    }

    setIsSubmitting(true);
    setUploadProgress(10);

    try {
      // Upload main picture
      toast.loading('Uploading images...');
      setUploadProgress(20);
      const mainImageUrl = await uploadImage(formData.mainPicture);
      setUploadProgress(40);

      // Upload additional images
      const additionalImages = [];
      if (formData.image1) {
        const url = await uploadImage(formData.image1);
        if (url) additionalImages.push({ url, caption: 'Additional Image 1' });
      }
      if (formData.image2) {
        const url = await uploadImage(formData.image2);
        if (url) additionalImages.push({ url, caption: 'Additional Image 2' });
      }
      if (formData.image3) {
        const url = await uploadImage(formData.image3);
        if (url) additionalImages.push({ url, caption: 'Additional Image 3' });
      }
      setUploadProgress(60);

      // Prepare video URLs
      const videos = [];
      if (formData.video1) videos.push({ url: formData.video1, platform: 'youtube' });
      if (formData.video2) videos.push({ url: formData.video2, platform: 'youtube' });
      if (formData.video3) videos.push({ url: formData.video3, platform: 'youtube' });

      // Create campaign data
      const campaignData = {
        title: formData.heading,
        shortDescription: formData.summary,
        description: formData.description,
        goalAmount: parseInt(formData.targetAmount),
        category: 'medical', // You can add a category selector
        images: [
          { url: `http://localhost:5000${mainImageUrl}`, caption: 'Main Campaign Image', isPrimary: true },
          ...additionalImages.map(img => ({ ...img, url: `http://localhost:5000${img.url}` }))
        ],
        videos: videos,
        timeline: {
          startDate: formData.startDate || new Date().toISOString(),
          endDate: formData.startDate 
            ? new Date(new Date(formData.startDate).setMonth(new Date(formData.startDate).getMonth() + 3)).toISOString()
            : new Date(new Date().setMonth(new Date().getMonth() + 3)).toISOString() // 3 months default
        },
        fundraiser: user._id,
        beneficiary: {
          name: formData.heading.split(' ')[0], // First word as name
          relationship: 'self', // Default relationship
          contact: {
            phone: formData.contact,
            email: user.email // Use logged in user's email
          }
        },
        location: {
          city: 'Mumbai',
          state: 'Maharashtra',
          country: 'India'
        }
      };

      setUploadProgress(80);
      toast.dismiss();
      toast.loading('Creating campaign...');

      // Create campaign
      const response = await campaignService.createCampaign(campaignData);
      setUploadProgress(100);

      toast.dismiss();
      toast.success('Campaign created successfully and is now LIVE! ðŸŽ‰');
      
      // Reset form
      handleReset();
      
      // Stay in admin panel - no automatic redirect
      // Just show success message and let admin manually navigate
      setTimeout(() => {
        // Optionally refresh the page to clear the form
        window.location.reload();
      }, 2000);

    } catch (error) {
      console.error('Campaign creation failed:', error);
      console.error('Validation errors:', error.response?.data?.errors); // Show detailed errors
      toast.dismiss();
      
      // Show specific validation errors
      if (error.response?.data?.errors && Array.isArray(error.response.data.errors)) {
        const errorMessages = error.response.data.errors
          .map(err => `${err.field}: ${err.message}`)
          .join('\n');
        toast.error(`Validation failed:\n${errorMessages}`, { duration: 5000 });
      } else {
        toast.error(error.response?.data?.message || error.message || 'Failed to create campaign');
      }
    } finally {
      setIsSubmitting(false);
      setUploadProgress(0);
    }
  };

  const handleReset = () => {
    if (isSubmitting) return;
    
    setFormData({
      mainPicture: null,
      summary: "",
      heading: "",
      contact: "",
      startDate: "",
      targetAmount: "",
      description: "",
      video1: "",
      video2: "",
      video3: "",
      image1: null,
      image2: null,
      image3: null
    });
    toast.success('Form reset');
  };

  return (
    <>
      <div className="mb-8">
        <h1 className="text-4xl font-bold text-gray-900 mb-2">Upload Fundraiser Campaign</h1>
      </div>

      <div className="bg-white rounded-xl shadow-sm border border-gray-100 p-8">
        <form onSubmit={handleSubmit} className="space-y-6">
          {/* Main Picture Upload */}
          <div>
            <label className="block text-sm font-medium text-red-600 mb-2">
              Upload Main Picture <span className="text-gray-500">(530 x 398 px)</span>
            </label>
            <div className="flex items-center gap-3">
              <label className="px-4 py-2 bg-gray-100 border border-gray-300 rounded-lg cursor-pointer hover:bg-gray-200 transition-colors text-sm font-medium text-gray-700">
                Choose file
                <input
                  type="file"
                  className="hidden"
                  onChange={(e) => handleFileChange(e, 'mainPicture')}
                  accept="image/*"
                />
              </label>
              <span className="text-sm text-gray-500">
                {formData.mainPicture ? formData.mainPicture.name : 'No file chosen'}
              </span>
            </div>
          </div>

          {/* Fundraiser Summary */}
          <div>
            <label className="block text-sm font-medium text-green-600 mb-2">
              Fundraiser summaries <span className="text-gray-500 text-xs">(Min 20 characters, Max 200 characters)</span>
            </label>
            <textarea
              name="summary"
              value={formData.summary}
              onChange={handleChange}
              placeholder="Enter short summary (minimum 20 characters)"
              rows={4}
              className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 text-sm"
            />
            <div className="text-right text-xs mt-1">
              <span className={formData.summary.length < 20 ? 'text-red-500' : formData.summary.length > 200 ? 'text-red-500' : 'text-green-600'}>
                {formData.summary.length} / 200 characters {formData.summary.length < 20 && `(${20 - formData.summary.length} more needed)`}
              </span>
            </div>
          </div>

          {/* Fundraiser Heading and Contact */}
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
            <div className="md:col-span-2">
              <label className="block text-sm font-medium text-green-600 mb-2">
                Fundraiser Heading <span className="text-gray-500 text-xs">(Min 10 characters, Max 100 characters)</span>
              </label>
              <input
                type="text"
                name="heading"
                value={formData.heading}
                onChange={handleChange}
                placeholder="Enter The Heading Of the Fundraiser (minimum 10 characters)"
                className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 text-sm"
              />
              <div className="text-right text-xs mt-1">
                <span className={formData.heading.length < 10 ? 'text-red-500' : formData.heading.length > 100 ? 'text-red-500' : 'text-green-600'}>
                  {formData.heading.length} / 100 characters {formData.heading.length < 10 && `(${10 - formData.heading.length} more needed)`}
                </span>
              </div>
            </div>
            <div>
              <label className="block text-sm font-medium text-green-600 mb-2">
                Amount To be Raised
              </label>
              <input
                type="number"
                name="targetAmount"
                value={formData.targetAmount}
                onChange={handleChange}
                placeholder="Enter Amount â‚¹"
                className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 text-sm"
              />
            </div>
          </div>

          {/* Contact and Start Date */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div>
              <label className="block text-sm font-medium text-green-600 mb-2">
                Fundraiser Contact <span className="text-gray-500 text-xs">(10 digit mobile number)</span>
              </label>
              <input
                type="text"
                name="contact"
                value={formData.contact}
                onChange={handleChange}
                placeholder="Enter 10 digit mobile number (e.g., 9876543210)"
                maxLength="10"
                className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 text-sm"
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-green-600 mb-2">
                Start Date Of Campaign
              </label>
              <input
                type="date"
                name="startDate"
                value={formData.startDate}
                onChange={handleChange}
                className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 text-sm"
              />
            </div>
          </div>

          {/* Fundraiser Description */}
          <div>
            <label className="block text-sm font-medium text-green-600 mb-2">
              Fundraiser Description <span className="text-gray-500 text-xs">(Min 50 characters, Max 5000 characters)</span>
            </label>
            <div className="border border-gray-300 rounded-lg p-2">
              <div className="flex items-center gap-2 mb-2 pb-2 border-b border-gray-200">
                <select className="text-sm border border-gray-300 rounded px-2 py-1">
                  <option>Normal</option>
                  <option>Heading 1</option>
                  <option>Heading 2</option>
                </select>
                <button type="button" className="p-1 hover:bg-gray-100 rounded">
                  <strong>B</strong>
                </button>
                <button type="button" className="p-1 hover:bg-gray-100 rounded italic">
                  I
                </button>
                <button type="button" className="p-1 hover:bg-gray-100 rounded underline">
                  U
                </button>
              </div>
              <textarea
                name="description"
                value={formData.description}
                onChange={handleChange}
                placeholder="Enter detailed description (minimum 50 characters)"
                rows={6}
                className="w-full px-2 py-1 focus:outline-none text-sm"
              />
            </div>
            <div className="text-right text-xs mt-1">
              <span className={formData.description.length < 50 ? 'text-red-500' : formData.description.length > 5000 ? 'text-red-500' : 'text-green-600'}>
                {formData.description.length} / 5000 characters {formData.description.length < 50 && `(${50 - formData.description.length} more needed)`}
              </span>
            </div>
          </div>

          {/* Video Links */}
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
            <div>
              <label className="block text-sm font-medium text-red-600 mb-2">
                Upload YT 1 Video Link
              </label>
              <input
                type="url"
                name="video1"
                value={formData.video1}
                onChange={handleChange}
                placeholder="Paste Link : E.g https://www.youtube.com/embed/qfeIymEYqT"
                className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 text-sm"
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-red-600 mb-2">
                Upload YT 2 Video Link
              </label>
              <input
                type="url"
                name="video2"
                value={formData.video2}
                onChange={handleChange}
                placeholder="Paste Link : E.g https://www.youtube.com/embed/qfeIymEYqT"
                className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 text-sm"
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-red-600 mb-2">
                Upload YT 3 Video Link
              </label>
              <input
                type="url"
                name="video3"
                value={formData.video3}
                onChange={handleChange}
                placeholder="Paste Link : E.g https://www.youtube.com/embed/qfeIymEYqT"
                className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 text-sm"
              />
            </div>
          </div>

          {/* Image Uploads */}
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
            <div>
              <label className="block text-sm font-medium text-red-600 mb-2">
                Upload image 1
              </label>
              <div className="flex items-center gap-3">
                <label className="px-4 py-2 bg-gray-100 border border-gray-300 rounded-lg cursor-pointer hover:bg-gray-200 transition-colors text-sm font-medium text-gray-700">
                  Choose file
                  <input
                    type="file"
                    className="hidden"
                    onChange={(e) => handleFileChange(e, 'image1')}
                    accept="image/*"
                  />
                </label>
                <span className="text-sm text-gray-500">
                  {formData.image1 ? formData.image1.name : 'No file chosen'}
                </span>
              </div>
            </div>
            <div>
              <label className="block text-sm font-medium text-red-600 mb-2">
                Upload image 2
              </label>
              <div className="flex items-center gap-3">
                <label className="px-4 py-2 bg-gray-100 border border-gray-300 rounded-lg cursor-pointer hover:bg-gray-200 transition-colors text-sm font-medium text-gray-700">
                  Choose file
                  <input
                    type="file"
                    className="hidden"
                    onChange={(e) => handleFileChange(e, 'image2')}
                    accept="image/*"
                  />
                </label>
                <span className="text-sm text-gray-500">
                  {formData.image2 ? formData.image2.name : 'No file chosen'}
                </span>
              </div>
            </div>
            <div>
              <label className="block text-sm font-medium text-red-600 mb-2">
                Upload image 3
              </label>
              <div className="flex items-center gap-3">
                <label className="px-4 py-2 bg-gray-100 border border-gray-300 rounded-lg cursor-pointer hover:bg-gray-200 transition-colors text-sm font-medium text-gray-700">
                  Choose file
                  <input
                    type="file"
                    className="hidden"
                    onChange={(e) => handleFileChange(e, 'image3')}
                    accept="image/*"
                  />
                </label>
                <span className="text-sm text-gray-500">
                  {formData.image3 ? formData.image3.name : 'No file chosen'}
                </span>
              </div>
            </div>
          </div>

          {/* Agreement Checkbox */}
          <div className="flex items-center gap-2">
            <input
              type="checkbox"
              id="agree"
              className="w-4 h-4 text-blue-600 border-gray-300 rounded focus:ring-blue-500"
              required
            />
            <label htmlFor="agree" className="text-sm text-green-600">
              I agree I want to Upload The Above Data On website
            </label>
          </div>

          {/* Progress Bar */}
          {isSubmitting && uploadProgress > 0 && (
            <div className="w-full">
              <div className="flex justify-between mb-1">
                <span className="text-sm font-medium text-blue-700">Uploading...</span>
                <span className="text-sm font-medium text-blue-700">{uploadProgress}%</span>
              </div>
              <div className="w-full bg-gray-200 rounded-full h-2.5">
                <div
                  className="bg-blue-600 h-2.5 rounded-full transition-all duration-300"
                  style={{ width: `${uploadProgress}%` }}
                ></div>
              </div>
            </div>
          )}

          {/* Submit Buttons */}
          <div className="flex items-center gap-4">
            <button
              type="submit"
              disabled={isSubmitting}
              className="px-6 py-2 bg-blue-600 text-white text-sm font-medium rounded-lg hover:bg-blue-700 transition-colors disabled:opacity-50 disabled:cursor-not-allowed flex items-center gap-2"
            >
              {isSubmitting ? (
                <>
                  <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white"></div>
                  Creating Campaign...
                </>
              ) : (
                'Submit'
              )}
            </button>
            <button
              type="button"
              onClick={handleReset}
              disabled={isSubmitting}
              className="px-6 py-2 bg-gray-600 text-white text-sm font-medium rounded-lg hover:bg-gray-700 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
            >
              Reset
            </button>
          </div>
        </form>
      </div>
    </>
  );
}



